			e.preventDefault();
			if ($(this).parent('li').find('ul').length > 0) {
				$(this).parent('li').find('ul').attr('aria-hidden', 'false').addClass(settings.menuHoverClass).find('a').attr('tabIndex', 0).first().focus();
			}
